<template>
  <div>
      <div class="seachbar">
        <div class="ctn">
            <img src="../assets/images/search.png" class="big">
            <input v-model="inputValue" @keyup.enter="search" type="search" placeholder="搜索" class="search">
        </div>
        <img src="../assets/images/BOZO.LOGO.png" class="logo">
      </div>

      <swiper class="move" :options="swiperOptions">
      <swiper-slide v-for="(item, index) in 5" :key="index">
        <img src="../assets/images/0099.png">
        <!-- <img :src="item.image" alt=""> -->
        <a :href="item.url"></a>
      </swiper-slide>
      <div class="swiper-pagination pagination-home"  slot="pagination"></div>
    </swiper>
      <div v-for="(item, i) in music" :key="i">
        <div @click="player(i)" class="info">
          <div class="photo">
            <img :src="item.icon" height="170px" class="icon">
            <img src="../assets/images/666555555555.png" height="165px" class="heijiao">
          </div>
          <div class="title">
            <h2>{{item.title}}</h2>
          </div>
          <div class="name">
            <span>{{item.name}}</span>
          </div>
          <div class="date">
            <span>发行日 {{item.createdAt.slice(0, 10)}}</span>
          </div>
        </div>
      </div>
      <div>
      <!-- <audio :src="playUrl" @canplay="getDuration" @timeupdate="updateTime" autoplay controls ref="audio"></audio> -->
    </div>
    <div class="bot">
    </div>
    <div class="player">
          <div class="play_icon">
            <img :src="playIcon1" class="icon">
            <img @click="ting" v-if="isPlay" src="../assets/images/pause.png" class="pausestyle">
            <img  @click="pao" v-else src="../assets/images/play.png" class="pausestyle">
          </div>
          <div class="song">
            {{playerName}}
          </div>
          <div class="bar">
              <!-- <div class="dialogAudioProgress" ref="dialogAudioProgress" @mousedown="controlAudioProgress($event)">
                <span class="progressDot" :style="dotStyle"></span>
                <span class="bar1" :style="progressStyle"></span>
              </div> -->
              <div class="progressbar" @click="playMusic" ref="runfatbar">
                  <div class="movebar" ref="runbar">
                      <span class="dot" draggable="true"></span>
                  </div>
              </div>
              <div class="howTime">
                <span>{{cTime}}</span>/<span>{{dTime}}</span>
                <audio :src="playUrl"  @ended="onEnded" ref="audio"></audio>
              </div>
              <div class="controls">
                  <i class="btn_prev" @click="prevMusic"><img src="../assets/images/prev.png" height="50px"></i>
                  <i class="btn_next" @click="nextMusic"><img src="../assets/images/next.png" height="50px"></i>
                  <i class="auto_next" @click="autoMusic" v-if="isNext"><img src="../assets/images/proper.png" height="50px"></i>
                  <i class="auto_loop" @click.stop="autoLoop" v-else-if="isLoop"><img src="../assets/images/loop.png" height="50px"></i>
                  <i class="auto_random" @click="autoRandom" v-else><img src="../assets/images/random.png" height="50px"></i>
              </div>
          </div>
      </div>
      <!-- <router-view></router-view> -->
  </div>
</template>

<script>
// import axios from 'axios'
export default {
    data () {
        return {
            cTime: '00:00',
            dTime: '00:00',
            inputValue:"",
            swiperOptions: {
            pagination: {
            el: '.pagination-home'
            }
        },
        music:[],
        playUrl: null,
        playIcon: {},
        playIcon1: require('../assets/images/666555555555.png'),
        index: 0,
        isNext: true,
        isLoop: false,
        playerName: '暂无播放歌曲',
        isPlay: true
        }
    },
    // computed: {
    //     playUrl() {
    //         return this.music[this.index].audio;
    //     }
    // },
    methods: {
        async fetchMusic(){
      const res = await this.$http.get('musics/list')
      this.music = res.data.reverse()
      // console.log(this.music);
    },
      async player(i) {
        const res = await this.music   //$http.get('musics/list')
        const change = await this.$refs.audio
        if(change.play){
          this.isPlay = false
          this.$nextTick(() => this.$refs.audio.play())
        }
        this.index = i
        this.playUrl = res[this.index].audio;
        this.playIcon1 = res[this.index].icon;
        this.playerName = res[this.index].title + '-' +res[this.index].name
      },
      // 上一首
      async prevMusic() {
        const res = await this.music   //$http.get('musics/list')
        const change = await this.$refs.audio
        if(change.play){
          this.isPlay = false
          this.$nextTick(() => this.$refs.audio.play())
        }
        // console.log(this.index);
            // console.log(this.index);
            let index = this.index - 1
            if(index < 0) {
                index = this.music.length - 1
            }
            this.index = index
            this.playUrl = res[this.index].audio;
        this.playIcon1 = res[this.index].icon;
        this.playerName = res[this.index].title + '-' +res[this.index].name
            // console.log(this.index);
            this.$nextTick(() => this.$refs.audio.play())
        },
        // 下一首
        async nextMusic() {
          const res = await this.music   //$http.get('musics/list')
          const change = await this.$refs.audio
        if(change.play){
          this.isPlay = false
          this.$nextTick(() => this.$refs.audio.play())
        }
          let index = this.index + 1
            if(index === this.music.length) {
                index = 0
            }
            this.index = index;
            this.playUrl = res[this.index].audio;
        this.playIcon1 = res[this.index].icon;
        this.playerName = res[this.index].title + '-' +res[this.index].name
			this.$nextTick(() => this.$refs.audio.play())
        },
        // 播放结束
    async onEnded() {
    //   this.audioImg = "./dialogDetailPlay.png";
    const res = await this.music      // $http.get('musics/list')
    const change = await this.$refs.audio
        
            if(this.isNext == true){
              let index = this.index + 1
            if(index === this.music.length) {
                index = 0
            }
            this.index = index;
            this.playUrl = res[this.index].audio;
        this.playIcon1 = res[this.index].icon;
        this.playerName = res[this.index].title + '-' + res[this.index].name
         this.cTime = "00:00";
      this.$refs.runbar.style.width = 0;
            }else if(this.isLoop == true) {
            let index = this.index
            this.index = index;
            this.playUrl = res[this.index].audio;
        this.playIcon1 = res[this.index].icon;
        this.playerName = res[this.index].title + '-' + res[this.index].name
         this.cTime = "00:00";
      this.$refs.runbar.style.width = 0;
            }else{
              let index = Math.floor(Math.random()*this.music.length) //.random().this.index
              // console.log(index);
            this.index = index;
            this.playUrl = res[this.index].audio;
        this.playIcon1 = res[this.index].icon;
        this.playerName = res[this.index].title + '-' + res[this.index].name
         this.cTime = "00:00";
      this.$refs.runbar.style.width = 0;
            }
            if(change.play){
          this.isPlay = false
          this.$nextTick(() => this.$refs.audio.play())
        }
			// this.$nextTick(() => this.$refs.audio.play())
     
    },
        // 顺序播放
        autoMusic() {
          // const res = await this.$http.get('musics/list')
          this.isNext = !this.isNext
          this.isLoop = !this.isLoop
        },
        autoLoop() {
          this.isLoop = !this.isLoop
        },
        // 随机播放
        autoRandom() {
          this.isNext = true
        },
    // go() {
    //     this.icon = !this.icon
    //     let music = this.$refs.audio
    //     if (this.icon) {
    //         music.pause()
    //     }else{
    //         music.play()
    //     }
    // },
    async ting() {
      const change = await this.$refs.audio
      if(change.play){
          this.isPlay = false
          change.play()
        }
    },
    async pao() {
      const change = await this.$refs.audio
      if(change.pause) {
        this.isPlay = true
        change.pause()
      }
    },
    
        
    playMusic(e) {
        const sing = this.$refs.audio
        const barWidth = e.layerX / this.$refs.runfatbar.offsetWidth
        this.$refs.runbar.style.width = `${barWidth * 100}%`
        sing.currentTime = sing.duration * barWidth
        // console.log(sing.currentTime);
        sing.play()
    }
    },
    
    mounted() {
        const sing = this.$refs.audio
        const singBar = this.$refs.runbar
        const singWidth = this.$refs.runfatbar.offsetWidth

        sing.addEventListener('canplay', ()=> {
            // console.log(e);
            const singTime = sing.duration
            const branch = Math.floor(singTime / 60)
            const secend = Math.ceil(singTime % 60)
            if(branch < 10 && secend < 10) {
                this.dTime =`0${branch}:0${secend}`
            }else if (branch < 10) {
                this.dTime = `0${branch}:${secend}`
            }else if (secend < 10) {
                this.dTime = `${branch}:0${secend}`
            }else {
                this.dTime = `${branch}:${secend}`
            }
        }),
        
        sing.addEventListener('timeupdate', () => {
            const singTime = sing.duration
            const stopTime =sing.currentTime
            singBar.style.width = `${(stopTime / singTime) * 100}%`
            const branch = Math.floor(stopTime / 60)
            const second = Math.floor(stopTime % 60)
            if ( branch < 10 && second < 10) {
                this.cTime =`0${branch}:0${second}`
            }else if (branch < 10) {
                this.cTime = `0${branch}:${second}`
            }else if (second < 10) {
                this.cTime = `${branch}:0${second}`
            }else {
                this.cTime = `${branch}:${second}`
            }
        }),

        singBar.addEventListener('touchmove', (event) => {
            // console.log(event);
            const events = event.targetTouches[0].pageX
            singBar.style.width = `${(events / singWidth) * 100}%`
            sing.pause()
        })
        singBar.addEventListener('touchend', () => {
            const touWidth = (singBar.offsetWidth / singWidth)
            sing.currentTime = sing.duration * touWidth
            sing.play()
            // console.log(sing.currentTime);
        })
    },
    
    created (){
    this.fetchMusic()
  },
    // updated () {
    //     this.timeUpdate(e) = this.cTime;
    // }
  
}

</script>

<style lang="scss">
.seachbar {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 980px;
    height: 150px;
    background: #ff0753;
    margin: 0 auto;
    z-index: 20;
    ul {
        width: 920px;
        height: 550px;
        margin: 150px auto 0;
        li {
            list-style: none;
            padding-top: 20px;
            div {
                height: 30px;
                span {
                    padding-right: 10px;
                }
            }
        }
    }
    .ctn {
        float: left;
        width: 790px;
        height: 80px;
        background: #fff;
        border-radius: 50px;
        margin-top: 35px;
        margin-left: 30px;
        .big {
            float: left;
            width: 41px;
            height: 41px;
            margin-top: 20px;
            margin-left: 20px;
            margin-right: 10px;
        }
        .search {
            float: left;
            width: 673px;
            height: 80px;
            font-size: 45px;
            background: #fff;
            outline: none;
            border: none;
        }
    }
    .logo {
            float: right;
            width: 120px;
            height: 120px;
            margin-top: 15px;
            margin-right: 30px;
        }
}

.move {
  width: 920px !important;
  height: 480px !important;
  margin-top: 170px;
  border-radius: 20px;
}
.pagination-home {
  .swiper-pagination-bullet {
    background: #fff;
    &.swiper-pagination-bullet-active {
      background: #fff;
    }
  }
}
.info {
  width: 920px;
  height: 170px;
  margin: 30px auto 0;
  .date {
    span {
      font-size: 28px;
      display: inline-block;
      margin: 10px 0 0 20px;
      color: #757575;
    }
  }
  .name {
    span {
      font-size: 30px;
      margin: 10px 0 0 20px;
      display: inline-block;
    }
  }
 .title {
    h2 {
    font-size: 35px;
    font-weight: 400;
    display: inline-block;
    margin: 10px 0 0 20px;
  }
 }
  .photo {
    float: left;
    position: relative;
    width: 245px;
    height: 170px;
    .heijiao {
      position: absolute;
      top: 3px;
      right: 5px;
    }
    .icon {
      position: absolute;
      top: 0;
      left: 0;
      width: 170px;
      height: 170px;
      border-radius: 10px;
      z-index: 5;
    }
  }
}

.bot {
  width: 980px;
  height: 300px;
}
.player {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  width: 980px;
  height: 220px;
  margin: 0 auto;
  background: #ff0753;
  z-index: 20;
  border-top-left-radius: 40px;
  border-top-right-radius: 40px;
  .play_icon {
      float: left;
      position: relative;
      margin: 25px 0 0 30px;
      height: 170px;
      width: 170px;
      .pausestyle {
        position: absolute;
        top: 53px;
        left: 53px;
        width: 65px;
        height: 65px;
      }
      .icon {
          width: 170px;
          height: 170px;
          border-radius: 50%;
          border: 5px solid #ffffff;
      }
  }
  .song {
      float: left;
      width: 660px;
      height: 3rem;
      margin: 25px 0 0 20px;
      font-size: 38px;
      overflow: hidden;
      color: #fff;
  }
//   进度条
  .bar {
      float: left;
      width: 730px;
      height: 50px;
      margin: 10px 0 0 20px;
      // 底部
      .controls {
        margin-top: 25px;
        i {
          padding-right: 50px;
        }
      }
      // 播放时间样式
      .howTime {
        float: right;
        font-size: 30px;
        color: #fff;
        margin-top: -23px;
      }
      // 进度条样式
      .progressbar {
            width: 550px;
            height: 10px;
            position: relative;
            border-radius: 50px;
            background: #e6e6e6;
            margin-top: 20px;
        }
        .movebar {
            height: 100%;
            border-radius: 50px;
            position: absolute;
            background: #fff;
        }
        .dot {
            display: inline-block;
            padding: 10px;
            border-radius: 50%;
            background: #fff;
            position: absolute;
            top: -5px;
            right: -5px;
            cursor: pointer;
        }
  }
}
</style>
